import React from "react";
import ArrowDropUpOutlinedIcon from "@mui/icons-material/ArrowDropUpOutlined";
import ArrowDropDownOutlinedIcon from "@mui/icons-material/ArrowDropDownOutlined";

export default function Totals(props) {
  const supabase = props.supabase;

  const colors = ["#2CB34A", "#FFC700", "#EF373E", "#00FFFF", "#FF00FF"];
  const [firstSeven, setFirstSeven] = React.useState();
  const [nextSeven, setNextSeven] = React.useState();

  const FetchLastSeven = async () => {
    const getCurrentPeriod = () => {
      const curr = new Date();
      const lastday1 = new Date(curr.setDate(curr.getDate()));
      const firstday1 = new Date(curr.setDate(curr.getDate() - 7));
      return { firstday1, lastday1 };
    };
    const { firstday1, lastday1 } = getCurrentPeriod();
    const { data: data1, error } = await supabase
      .from("Produce")
      .select(
        `weight,
                timestamp
          `
      )
      .lte("timestamp", lastday1.toISOString())
      .gt("timestamp", firstday1.toISOString());
    //.eq(society_id)
    var data = [];
    data1.forEach((produce) => {
      const weight = produce.weight;
      data.push(weight);
    });
    data = data.reduce((a, b) => a + b, 0);
    setFirstSeven(data);
  };

  const FetchNextSeven = async () => {
    const getCurrentPeriod = () => {
      const curr = new Date();
      const lastday = new Date(curr.setDate(curr.getDate() - 7));
      const firstday = new Date(curr.setDate(curr.getDate() - 7));
      return { firstday, lastday };
    };
    const { firstday, lastday } = getCurrentPeriod();
    const { data: data1, error } = await supabase
      .from("Produce")
      .select(
        `weight,
                timestamp
          `
      )
      .lte("timestamp", lastday.toISOString())
      .gt("timestamp", firstday.toISOString());
    //.eq(society_id)
    var data = [];
    data1.forEach((produce) => {
      const weight = produce.weight;
      data.push(weight);
    });
    data = data.reduce((a, b) => a + b, 0);
    setNextSeven(data);
  };

  FetchLastSeven();
  FetchNextSeven();
  const totalChange = firstSeven - nextSeven;
  const percent = ((totalChange / (firstSeven + nextSeven)) * 100).toFixed(1);
  const Percentage = () => {
    return totalChange > 5 ? (
      <>
        <ArrowDropUpOutlinedIcon style={{ color: "#2CB34A" }} />
        <p style={{ color: "#2CB34A" }}>
          {Math.abs(totalChange).toFixed(1)} ({percent}%) from previous week
          period
        </p>
      </>
    ) : (
      <>
        <ArrowDropDownOutlinedIcon style={{ color: "#FF0000" }} />
        <p style={{ color: "#FF0000" }}>
          {Math.abs(totalChange).toFixed(1)} ({percent}%) from previous week
          period
        </p>
      </>
    );
  };
  return (
    <div className="totals-content">
      <h1>Total Collection</h1>
      <div className="totals-number flex">
        <h1>{Math.round(firstSeven)}</h1>
        <p>Kgs</p>
      </div>
      <div className="totals-perc flex">
        <Percentage />
      </div>
    </div>
  );
}
