export default function Table(props) {
  const supabase = props.supabase;

  const [fetched, setFetched] = React.useState([]);

  const Fetch = async () => {
    const { data: data1, error } = await supabase
      .from("Produce")
      .select(
        `clerk_id,
          farmer_id,
          location,
          weight,
          produce_id,
          timestamp,
          Farmers(farmer_name),
          Clerks(clerk_name)
          `
      )
      .limit(12)
      .order("timestamp", { ascending: false });

    setFetched(data1);
    //console.log(fetched)
    //console.log(error)
  };
  React.useEffect(() => {
    Fetch();
  }, []);

  supabase
    .channel("public:Produce")
    .on(
      "postgres_changes",
      { event: "*", schema: "public", table: "Produce" },
      (payload) => {
        Fetch();
      }
    )
    .subscribe();
  /*
    const { data: data1, error } = await supabase
    .from('Produce')
    .select(`Clerks(clerk_name),
            Farmers(farmer_name)
    `)
    .eq('produce_id', payload.produce_id)
  
*/

  return (
    <div className="table">
      <table className="table-table">
        <thead>
          <tr>
            <th>Produce Id</th>
            <th>Weight</th>
            <th>Farmer Id</th>
            <th>Farmer Name</th>
            <th>Clerk Id</th>
            <th>Clerk Name</th>

            <th>Location</th>
            <th>Timestamp</th>
          </tr>
        </thead>
        <tbody>
          {fetched.map((val, key) => {
            return (
              <tr key={key}>
                <td>{val.produce_id}</td>
                <td>{val.weight}</td>
                <td>{val.farmer_id}</td>
                <td>{val.Farmers.farmer_name}</td>
                <td>{val.clerk_id}</td>
                <td>{val.Clerks.clerk_name}</td>

                <td>{val.location}</td>
                <td>
                  {val.timestamp.split("T")[0]}{" "}
                  {val.timestamp.split("T")[1].split(".")[0]}
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}
