import React from "react";
import { PieChart, Pie, Sector, Cell, Legend, Tooltip } from "recharts";

const colors = ["#2CB34A", "#FFC700", "#EF373E"];

export default function Monthly(props) {
  const supabase = props.supabase;

  const colors = ["#2CB34A", "#FFC700", "#EF373E", "#00FFFF", "#FF00FF"];
  const [data, setData] = React.useState([]);

  const getCurrentPeriod = () => {
    const curr = new Date();
    curr.setHours(0, 0, 0, 0);
    const lastday = new Date(curr);
    const firstday = new Date(curr.setDate(1));
    return { firstday, lastday };
  };

  const FetchCentres = async () => {
    const { firstday, lastday } = getCurrentPeriod();
    const { data: data1, error } = await supabase
      //.from('Farmers')
      //.select('farmer_id,phone_number,Society(society_name)')
      .from("Clerks")
      .select(
        `clerk_name,
              Produce(weight, timestamp),

              CollectionCentres(centre_name)
      `
      )
      .lte("Produce.timestamp", lastday.toISOString())
      .gt("Produce.timestamp", firstday.toISOString());
    //.eq(society_id)
    var data = {};

    data1.forEach((clerkDetails) => {
      const collection = clerkDetails.CollectionCentres.centre_name;

      clerkDetails.Produce.forEach((produce) => {
        const weight = produce.weight;
        data[collection] ??= 0.0;
        data[collection] += weight;
      });
    });

    const centreData = Object.entries(data).map(([centre, weight]) => {
      return { name: centre, value: weight };
    });

    setData(centreData);
  };
  React.useEffect(() => {
    FetchCentres();
  }, []);

  return (
    <div>
      <p className="pie-title bold">Monthly Analysis</p>
      <PieChart width={280} height={130} margin={{ top: 0, left: 30 }}>
        <Legend layout="vertical" verticalAlign="middle" align="right" />
        <Tooltip />
        <Pie
          data={data}
          cx={40}
          cy={65}
          innerRadius={35}
          outerRadius={50}
          startAngle={90}
          endAngle={-360}
          fill="#8884d8"
          paddingAngle={0}
          dataKey="value"
        >
          {data.map((entry, index) => (
            <Cell key={`cell-${index}`} fill={colors[index % colors.length]} />
          ))}
        </Pie>
      </PieChart>
    </div>
  );
}
