import React from "react";
import Navbar from "./components/Navbar";
import Home from "./Pages/Home";
import Login from "./Pages/Login";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import { createClient } from "@supabase/supabase-js";

//<Login setToken = {setToken} token={token}/>
export default function App() {
  const supabaseUrl = process.env.REACT_APP_SUPABASE_URL;
  const supabaseAnonKey = process.env.REACT_APP_SUPABASE_ANON_KEY;
  const serviceKey=process.env.REACT_APP_SUPABASE_SERVICE

  const supabase = createClient(supabaseUrl, serviceKey);
  const [isLogged, setIsLogged] = React.useState("");
  const [societyId, setSocietyId] = React.useState("");

  const updateSociety = async (id) => {
    const { data: data, error } = await supabase
      .from("SocietyAdmins")
      .select("society_id")
      .eq("auth_id", id);
    setSocietyId(data[0].society_id);
    
  };

  supabase.auth.onAuthStateChange((event, session) => {
    event === "SIGNED_IN" ? setIsLogged(true) : setIsLogged(false);
    //console.log(isLogged)

    if (event === "SIGNED_IN") {
      //console.log(session.user.id)
      updateSociety(session.user.id);
    }
  });
  
  //LOGIN ROUTES UNCOMMENT

  if (!isLogged) {
    return <Login supabase={supabase} societyId={societyId} />;
  } else {
    return <Home supabase={supabase} societyId={societyId} />;
  }

  /*
  const [token, setToken] = React.useState()
  return (
    <div className='app'>
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Home supabase ={supabase}/>}>
          </Route>
          <Route path="/login" element={<Login supabase ={supabase}/>}>
          </Route>
        </Routes>
      </BrowserRouter>
    </div>
    
  ); */
}
