import React from "react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
  Legend,
} from "recharts";

//<ResponsiveContainer width="95%" height={400}>
export default function Centres(props) {
  const supabase = props.supabase;

  const colors = ["#2CB34A", "#FFC700", "#EF373E", "#00FFFF", "#FF00FF"];
  const [data, setData] = React.useState([]);

  const formatDate = (date) =>
    new Date(date).toLocaleString("en-US", {
      timeZone: "Africa/Nairobi",
      day: "numeric",
      month: "short",
    });

  const getCurrentPeriod = () => {
    const curr = new Date();
    curr.setHours(0, 0, 0, 0);
    const lastday = new Date(curr.setDate(curr.getDate()));
    const firstday = new Date(curr.setDate(curr.getDate() - 9));
    return { firstday, lastday };
  };

  const FetchCentres = async () => {
    const { firstday, lastday } = getCurrentPeriod();
    const { data: data1, error } = await supabase
      //.from('Farmers')
      //.select('farmer_id,phone_number,Society(society_name)')
      .from("Clerks")
      .select(
        `clerk_name,
              Produce(weight, timestamp),

              CollectionCentres(centre_name)
      `
      )
      .lte("Produce.timestamp", lastday.toISOString())
      .gt("Produce.timestamp", firstday.toISOString());
    //.eq(society_id)
    var data = {};
    const centreData = [];
    data1.forEach((clerkDetails) => {
      const collection = clerkDetails.CollectionCentres.centre_name;
      data[collection] ??= {};

      clerkDetails.Produce.forEach((produce) => {
        const day = formatDate(produce.timestamp);
        const weight = produce.weight;
        data[collection][day] ??= 0.0;
        data[collection][day] += weight;
      });
    });

    for (let day = firstday; day < lastday; day.setDate(day.getDate() + 1)) {
      var dayLabel = formatDate(day);
      var dailyData = { date: dayLabel };

      for (const [collection, value] of Object.entries(data)) {
        dailyData[collection] = value[dayLabel] ?? 0;
      }
      centreData.push(dailyData);
    }
    setData(centreData);
  };
  React.useEffect(() => {
    FetchCentres();
  }, []);

  var centres = [];
  if (data.length > 0) {
    centres = Object.keys(data[0]).filter((name) => name != "date");
  }
  //console.log(centres)
  return (
    <LineChart
      width={700}
      height={330}
      data={data}
      margin={{
        top: 30,
        right: 30,
        left: 20,
        bottom: 5,
      }}
    >
      <CartesianGrid horizontal={false} vertical={true} stroke="#21130d" />
      <XAxis xAxisId="0" dataKey="date" axisLine={false} />
      <YAxis
        label={{ value: "No of Kgs(,000)", angle: -90, position: "insideLeft" }}
      />
      <Tooltip />
      <Legend
        layout="horizontal"
        verticalAlign="top"
        align="center"
        margin={{ bottom: 30 }}
      />
      {centres.map((item, index) => (
        <Line
          type="monotone"
          dataKey={item}
          stroke={colors[index % colors.length]}
          dot={false}
        />
      ))}
    </LineChart>
  );
}
