import React from "react";
import { Avatar } from '@mui/material';
import ArrowDropDownOutlinedIcon from '@mui/icons-material/ArrowDropDownOutlined';
import Settings from "../Popups/Settings";
import Register from "../Popups/Register";




export default function Navbar(props){

//
    const [isOpen, setIsOpen] = React.useState(false);
 
    const togglePopup = () => {
     setIsOpen(!isOpen);
    }

//

    const [isOpenForm, setIsOpenForm] = React.useState(false);
    
    const supabase = props.supabase
    const FetchAdmin = async () => {
        const{data: data1, error}= await supabase
        .from('SocietyAdmins')
        .select(`admin_name,
                society_id
        `)
        console.log(data1)
    }

    

    const togglePopupForm = () => {
    setIsOpenForm(!isOpenForm);
    }

    

    const username = 'username'
    const society = 'society'
    
    return(
        <div className="navbar">
            <img className="navbar-logo" src ="./images/new-saf-logo (1).svg" alt = "img"/>
            {props.login && <Avatar/>}
            {props.login &&<p>{username}@{society}</p>}
            {props.login &&<ArrowDropDownOutlinedIcon  onClick={togglePopup} />}
            
            {isOpen && <Settings
                supabase={props.supabase}
                handleClose={togglePopup}
                handleForm = {togglePopupForm}
    />}

            {isOpenForm && <Register
                            supabase={props.supabase}
                            societyId={props.societyId}
                            handleClose={togglePopupForm}
                            handleCloseAll={togglePopup}
                />}

        </div>
        

    )
}